import Vue from "vue";
import FInput from '@/components/input/form.vue';
import FList from '@/components/output/list.vue';
//export default class Hello extends Vue {
export default Vue.extend({
    name: "Root",
    components: {
        FInput,
        FList,
    },
    data() {
        return {
            tasks: [],
            t: ""
        };
    },
    created: function () { },
    methods: {
        addTask: function () {
            if ("" != this.t) {
                this.tasks.push(this.t);
                this.t = "";
            }
        }
    }
});



